const SET_CROPPER_MODAL_TOGGLE_REQUEST = 'SET_CROPPER_MODAL_TOGGLE_REQUEST';//   
const SET_CROPPER_SRC_RESULT_REQUEST = 'SET_CROPPER_SRC_RESULT_REQUEST';//  e.target.result  

const SET_CROPPER_MODAL_INIT = 'SET_CROPPER_MODAL_INIT'; //  

//s3 blob 
const API_CROPPER_THUMBNAIL_SEND_REQUEST = 'API_CROPPER_THUMBNAIL_SEND_REQUEST';
const API_CROPPER_THUMBNAIL_SEND_SUCCESS = 'API_CROPPER_THUMBNAIL_SEND_SUCCESS';
const API_CROPPER_THUMBNAIL_SEND_FAILURE = 'API_CROPPER_THUMBNAIL_SEND_FAILURE';

//s3     
const API_THUMBNAIL_REMOVE_REQUEST = 'API_THUMBNAIL_REMOVE_REQUEST';
const API_THUMBNAIL_REMOVE_SUCCESS = 'API_THUMBNAIL_REMOVE_SUCCESS';
const API_THUMBNAIL_REMOVE_FAILURE = 'API_THUMBNAIL_REMOVE_FAILURE';

const initialState = {
    loading: false,
    open: false,
    error: {
        error: false,
        type: null
    },
    file: {
        src: null,
        name: null,
        type: null,
        size: null,
        originName: null,
    }
};

export function cropperReducer(state = initialState, action) {
    switch (action.type) {
        case SET_CROPPER_MODAL_TOGGLE_REQUEST:
            return {...state, loading: false, open: !state.open, error: state.error, file: state.file};
        case SET_CROPPER_SRC_RESULT_REQUEST:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                file: {
                    src: action.src,
                    name: action.fileName,
                    type: action.fileType,
                    size: action.size,
                    originName: action.originName,
                }
            };
        case API_CROPPER_THUMBNAIL_SEND_REQUEST: //    
            return {
                ...state,
                loading: true,
                open: state.open,
                error: state.error,
                file: state.file
            };
        case API_CROPPER_THUMBNAIL_SEND_SUCCESS: //
            return {
                ...state,
                loading: false,
                open: false,
                error: initialState.error,
                file: state.file
            };
        case API_CROPPER_THUMBNAIL_SEND_FAILURE: // 
            return {
                ...state,
                loading: false,
                open: false,
                error: action.error,
                file: initialState.file
            };
        case API_THUMBNAIL_REMOVE_REQUEST:
            return{
                ...state,
                loading:true,
                open:false,
                error:initialState.error,
                file:initialState.file
            };
        case API_THUMBNAIL_REMOVE_SUCCESS:
            return{
                ...state,
                loading:false,
                open:false,
                error:initialState.error,
                file:initialState.file
            };
        case API_THUMBNAIL_REMOVE_FAILURE:
            return{
                ...state,
                loading:false,
                open:false,
                error:action.error,
                file:initialState.file
            };
        case SET_CROPPER_MODAL_INIT:
            return initialState;
        default:
            return state;
    }
}