import axios from "axios";

export const checkDuplicatedEmail = (email) => {
    return axios({
        method: 'post',
        url: '/signup/emailcheck',
        data: {
            email: email
        }
    });//axios
};

//   .
//isDuplicated: false =>   
//isDuplicated: true =>  
export const checkDuplicatedNickname = (value, clientIdx) => {
    return axios({
        method: 'POST',
        url: '/signup/nicknamecheck',
        data: {
            nickname: value,
            clientIdx: clientIdx
        }
    });
};
