import {put, takeEvery, call} from "redux-saga/effects";
import axios from 'axios'

export function* watcherPhoneAuth() {
    yield takeEvery('API_FIND_BY_PHONE_REQUEST', phonePasswordSaga); // 
    yield takeEvery('API_PHONE_AUTH_REQUEST', nexmoVerifyRequestSaga); // 
    yield takeEvery('API_PHONE_AUTH_VERIFY_CODE_REQUEST', nexmoDigitVerifyCheck);//  
    yield takeEvery('API_FIND_ID_BY_PHONE_REQUEST', findIdSaga); // 
}


//     
function nexmoDigitAxios({country, phone}) {
    return axios({
        method: 'post',
        url: '/nexmo/request',
        data: {
            country: country,
            phone: phone
        }
    })
}

//     
function* nexmoVerifyRequestSaga(req) {

    const country = req.country;
    const phone = req.phone;
    try {
        const response = yield call(nexmoDigitAxios, {country, phone});
        console.log(response.data);

        if (response.data.success) {
            yield put({
                type: 'API_PHONE_AUTH_SUCCESS',
                requestId: response.data.requestId
            })
        }
    } catch (e) {
        console.log(e);
        yield put({
            type: 'API_PHONE_AUTH_FAILURE',
        })
    }
}

//      
function nexmoDigitVerifyRequest(obj) {

    console.log(obj);

    const phone = obj.phone;
    const country = obj.country;
    const code = obj.code;
    const requestId = obj.requestId;
    const clientIdx = obj.clientIdx;

    return axios({
        method: 'POST',
        url: '/nexmo/codecheck',
        data: {
            phone: phone,
            country: country,
            code: code,
            requestId: requestId,
            clientIdx: clientIdx
        }
    })
}

function* nexmoDigitVerifyCheck(value) {

    const obj = {
        phone: value.phone,
        requestId: value.requestId,
        code: value.code,
        country: value.country
    };

    console.log(obj);

    try {
        const response = yield call(nexmoDigitVerifyRequest, obj);
        if (response.data.success) {
            yield put({
                type: 'API_PHONE_AUTH_VERIFY_CODE_SUCCESS'
            });
        } else {
            throw response.data
        }
    } catch (e) {
        console.log(e);
        yield put({
            type: 'API_PHONE_AUTH_VERIFY_CODE_FAILURE'
        });
    }
}


//    

function findPasswordAxios(object) {
    //   


    return axios({
        method: 'POST',
        data: {
            phone: object.phone,
            email: object.email,
            requestId: object.requestId,
            code: object.code,
            country: object.country
        },
        url: "/nexmo/phone/password"
    });
}

//   -
// 4               .
function* phonePasswordSaga(obj) {

    const object = obj.object;

    try {
        const response = yield call(findPasswordAxios, object);
        console.log(response);
        if (response.data.success) {
            yield put({
                type: 'API_FIND_BY_PHONE_SUCCESS',
            });
        } else {
            throw response.data
        }

    } catch (e) {
        console.log(e);
        yield put({
            type: 'API_FIND_BY_PHONE_FAILURE',
            error: e
        });
    }

}

//       

function findIdAxios(value) {

    console.log(value);
    return axios({
        method: 'POST',
        url: '/nexmo/phone/id',
        data: {
            phone: value.phone,
            requestId: value.requestId,
            code: value.code,
            country: value.country
        }
    });
}

//   
function* findIdSaga(obj) {

    const object = obj.object;

    try {
        const response = yield call(findIdAxios, object);

        if (response.data.success) {
            yield put({
                type: 'API_FIND_ID_BY_PHONE_SUCCESS',
                email:response.data.email
            });
        } else {
            throw response.data
        }

    } catch (e) {
        console.log(e);
        yield put({
            type: 'API_FIND_ID_BY_PHONE_FAILURE',
            error: e
        })
    }
}