// 
const SET_PHONE_AUTH_MODAL_OPEN_TOGGLE = 'SET_PHONE_AUTH_MODAL_OPEN_TOGGLE'; // 
const SET_PHONE_AUTH_CANCEL_NEXT_STEP_REQUEST = 'SET_PHONE_AUTH_CANCEL_NEXT_STEP_REQUEST';//  
const SET_COUNTRY_LIST_TOGGLE_REQUEST = 'SET_COUNTRY_LIST_TOGGLE_REQUEST';
const SET_COUNTRY_SELECTOR_REQUEST = 'SET_COUNTRY_SELECTOR_REQUEST';
const SET_PHONE_NUMBER_REQUEST = 'SET_PHONE_NUMBER_REQUEST';
const SET_PHONE_AUTH_DIGIT_CODE_REQUEST = 'SET_PHONE_AUTH_DIGIT_CODE_REQUEST';
const SET_PHONE_AUTH_EMAIL_REQUEST = 'SET_PHONE_AUTH_EMAIL_REQUEST';

//    
const API_PHONE_AUTH_REQUEST = 'API_PHONE_AUTH_REQUEST';
const API_PHONE_AUTH_SUCCESS = 'API_PHONE_AUTH_SUCCESS';
const API_PHONE_AUTH_FAILURE = 'API_PHONE_AUTH_FAILURE';

//    
const API_PHONE_AUTH_VERIFY_CODE_REQUEST = 'API_PHONE_AUTH_VERIFY_CODE_REQUEST';
const API_PHONE_AUTH_VERIFY_CODE_SUCCESS = 'API_PHONE_AUTH_VERIFY_CODE_SUCCESS';
const API_PHONE_AUTH_VERIFY_CODE_FAILURE = 'API_PHONE_AUTH_VERIFY_CODE_FAILURE';

//    
const API_FIND_BY_PHONE_REQUEST = 'API_FIND_BY_PHONE_REQUEST';
const API_FIND_BY_PHONE_SUCCESS = 'API_FIND_BY_PHONE_SUCCESS';
const API_FIND_BY_PHONE_FAILURE = 'API_FIND_BY_PHONE_FAILURE';

//    
const API_FIND_ID_BY_PHONE_REQUEST = 'API_FIND_ID_BY_PHONE_REQUEST';
const API_FIND_ID_BY_PHONE_SUCCESS = 'API_FIND_ID_BY_PHONE_SUCCESS';
const API_FIND_ID_BY_PHONE_FAILURE = 'API_FIND_ID_BY_PHONE_FAILURE';

// 
const API_PHONE_AUTH_INIT = 'API_PHONE_AUTH_INIT';


const initialState = {
    loading: false,
    open: false,
    error: {
        error: false,
        type: null,
    },
    auth: {
        success: false, //  
        email: null, //  ()
        countryList: false, // 
        nextStep: false, //true  
        requestId: null, //     
        phone: null, // 
        country: 'us', //
        code: null, //4 
    },
    findResult:{
        email:null
    }
};


export function phoneAuthReducer(state = initialState, action) {
    switch (action.type) {
        case SET_PHONE_AUTH_MODAL_OPEN_TOGGLE:
            return {
                ...state,
                open: !state.open,
                loading: false,
                error: state.error,
                auth: {
                    success: false, //  
                    email: state.auth.email, //  ()
                    countryList: false, // 
                    nextStep: false, //true  
                    requestId: null, //     
                    phone: null, // 
                    country: 'us', //
                    code: null, //4 
                },
                findResult:{
                    email:null
                }
            };
        case SET_PHONE_AUTH_CANCEL_NEXT_STEP_REQUEST:
            return {
                ...state,
                open: state.open,
                loading: false,
                error: state.error,
                auth: {
                    success: false, //  
                    email: state.auth.email, //  ()
                    countryList: false, // 
                    nextStep: false, //true  
                    requestId: null, //     
                    phone: null, // 
                    country: 'us', //
                    code: null, //4 
                },
                findResult:{
                    email:null
                }
            }
        case SET_COUNTRY_LIST_TOGGLE_REQUEST:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                auth: {
                    success: state.auth.success, //  
                    email: state.auth.email, //  ()
                    countryList: !state.auth.countryList, // 
                    nextStep: state.auth.nextStep, //true  
                    requestId: state.auth.requestId, //     
                    phone: state.auth.phone, // 
                    country: state.auth.country, //
                    code: state.auth.code, //4 
                },
                findResult:{
                    email:null
                }
            };
        case SET_PHONE_AUTH_DIGIT_CODE_REQUEST:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                auth: {
                    success: state.auth.success, //  
                    email: state.auth.email, //  ()
                    countryList: state.auth.countryList, // 
                    nextStep: state.auth.nextStep, //true  
                    requestId: state.auth.requestId, //     
                    phone: state.auth.phone, // 
                    country: state.auth.country, //
                    code: action.code, //4 
                },
                findResult:{
                    email:null
                }
            };
        case SET_COUNTRY_SELECTOR_REQUEST:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                auth: {
                    success: state.auth.success, //  
                    email: state.auth.email, //  ()
                    countryList: false, // 
                    nextStep: state.auth.nextStep, //true  
                    requestId: state.auth.requestId, //     
                    phone: state.auth.phone, // 
                    country: action.country, //
                    code: state.auth.code, //4 
                },
                findResult:{
                    email:null
                }
            };
        case SET_PHONE_NUMBER_REQUEST:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                auth: {
                    success: state.auth.success, //  
                    email: state.auth.email, //  ()
                    countryList: state.auth.countryList, // 
                    nextStep: state.auth.nextStep, //true  
                    requestId: state.auth.requestId, //     
                    phone: action.phone, // 
                    country: state.auth.country, //
                    code: state.auth.code, //4 
                },
                findResult:{
                    email:null
                }
            };
        case API_PHONE_AUTH_REQUEST: //  
            return {
                ...state,
                loading: true,
                open: state.open,
                error: state.error,
                auth: state.auth,
                findResult:state.findResult
            };
        case API_PHONE_AUTH_SUCCESS: //     
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                auth: {
                    success: state.auth.success, //   
                    email: state.auth.email, //  ()
                    countryList: state.auth.countryList, // 
                    nextStep: true, //true  
                    requestId: action.requestId, //     
                    phone: state.auth.phone, // 
                    country: state.auth.country, //
                    code: state.auth.code, //4 
                },
                findResult:{
                    email:null
                }
            };
        case API_PHONE_AUTH_FAILURE:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: action.error,
                auth: {
                    success: false, //  
                    email: null, //  ()
                    countryList: false, // 
                    nextStep: false, //true  
                    requestId: null, //     
                    phone: null, // 
                    country: state.auth.country, //
                    code: null, //4 
                },
                findResult:{
                    email:null
                }
            };
        case API_PHONE_AUTH_VERIFY_CODE_REQUEST:
            return {
                ...state,
                loading: true,
                open: state.open,
                error: state.error,
                auth: state.auth,
                findResult:state.findResult
            };
        case API_PHONE_AUTH_VERIFY_CODE_SUCCESS:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                auth: {
                    success: true, //  
                    email: null, //  ()
                    countryList: false, // 
                    nextStep: false, //true  
                    requestId: null, //     
                    phone: null, // 
                    country: state.auth.country, //
                    code: null, //4 
                },
                findResult:{
                    email:null
                }
            };
        case API_PHONE_AUTH_VERIFY_CODE_FAILURE:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: action.error,
                auth: {
                    success: false, //  
                    email: null, //  ()
                    countryList: false, // 
                    nextStep: false, //true  
                    requestId: null, //     
                    phone: null, // 
                    country: state.auth.country, //
                    code: null, //4 
                },
                findResult:{
                    email:null
                }
            };
        case API_FIND_BY_PHONE_REQUEST: //    
            return {
                ...state,
                loading: true,
                open: state.open,
                error: state.error,
                auth: state.auth,
                findResult:state.findResult
            };
        case API_FIND_BY_PHONE_SUCCESS: //    
            return {
                ...state,
                loading: false,
                open: false,
                error: state.error,
                auth: {
                    success: true, //  
                    email: state.auth.email, //  ()
                    countryList: false, // 
                    nextStep: false, //true  
                    requestId: null, //     
                    phone: state.auth.phone, // 
                    country: state.auth.country, //
                    code: null, //4 
                },
                findResult:{
                    email:null
                }
            };
        case API_FIND_BY_PHONE_FAILURE:
            return {
                ...state,
                loading: false,
                open: false,
                error: action.error,
                auth: {
                    success: false, //  
                    email: state.auth.email, //  ()
                    countryList: false, // 
                    nextStep: false, //     nextStep false
                    requestId: null, //     
                    phone: null, // 
                    country: state.auth.country, //
                    code: null, //4 
                },
                findResult:{
                    email:null
                }
            };
        case SET_PHONE_AUTH_EMAIL_REQUEST:
            return {
                ...state,
                loading: false,
                open: state.open,
                error: state.error,
                auth: {
                    email: action.email,
                    success: state.auth.success,
                    nextStep: state.auth.nextStep,
                    countryList: state.auth.countryList,
                    requestId: state.auth.requestId,
                    phone: state.auth.phone,
                    country: state.auth.country,
                    code: state.auth.code,
                },
                findResult:initialState.findResult
            };
        case API_FIND_ID_BY_PHONE_REQUEST:            //    
            return {
                ...state,
                loading: true,
                open: state.open,
                error: state.error,
                auth: {
                    success: state.auth.success,
                    email: state.auth.email,
                    nextStep: state.auth.nextStep,
                    countryList: state.auth.countryList,
                    requestId: state.auth.requestId,
                    phone: state.auth.phone,
                    country: state.auth.country,
                    code: state.auth.code,
                },
                findResult:{
                    email:initialState.findResult
                }
            };
        case API_FIND_ID_BY_PHONE_SUCCESS: //    
            return {
                ...state,
                loading:false,
                open:false, // 
                error:state.error,
                auth:{
                    success: true,
                    email: null,
                    countryList: false, // 
                    nextStep: false, //true  
                    requestId: null, //     
                    phone: null, // 
                    country: initialState.auth.country, //
                    code: null, //4 
                },
                findResult:{
                    email:action.email
                }
            };
        case API_FIND_ID_BY_PHONE_FAILURE: //   
            return {
                ...state,
                loading:false,
                open:state.open,
                error:action.error,
                auth:initialState.auth,
                findResult:initialState.findResult
            };
        case API_PHONE_AUTH_INIT:
            return initialState;
        default:
            return state;
    }
}