//  state
const SET_WINDOW_WIDTH_REQUEST = 'SET_WINDOW_WIDTH_REQUEST';

//   
const API_WEB_LOGIN_REQUEST = 'API_WEB_LOGIN_REQUEST';
const API_WEB_LOGIN_SUCCESS = 'API_WEB_LOGIN_SUCCESS';
const API_WEB_LOGIN_FAILURE = 'API_WEB_LOGIN_FAILURE';

//   
const API_WEB_LOGOUT_REQUEST = 'API_WEB_LOGOUT_REQUEST';

//    
const REFRESH_SESSION_REQUEST = 'REFRESH_SESSION_REQUEST';

//     

//session 
const initialState = {
    loading: false,
    width: window.innerWidth && document.documentElement.clientWidth ?
        Math.min(window.innerWidth, document.documentElement.clientWidth) :
        window.innerWidth ||
        document.documentElement.clientWidth ||
        document.getElementsByTagName('body')[0].clientWidth,
    session: {
        _id: null,
        type: null,
        email: null,
        country: null,
        birth: {
            year: null,
            month: null,
            date: null,
        },
        gender: null,
        grade: null,
        point: null,
        name: {
            first: null,
            last: null
        },
        nickname: null,
        phone: null,
        status: {
            admin: null,
            visit: null,
            token: null,
            lastFindId: null,
            lastFindPw: null,
            lastModifiedPw: null,
            lastVisit: null,
            signupDate: null,
            lastModifiedThumbnail:null,
            social: []
        },
        thumbnail:null
    },
    login: {
        loading: false,
        error: {
            error: false,
            type: null
        },
        isLogin: false //false      , true
    }

};

export function clientStatusReducer(state = initialState, action) {
    switch (action.type) {
        case SET_WINDOW_WIDTH_REQUEST:
            return {
                ...state,
                loading: state.loading,
                width: window.innerWidth && document.documentElement.clientWidth ?
                    Math.min(window.innerWidth, document.documentElement.clientWidth) :
                    window.innerWidth ||
                    document.documentElement.clientWidth ||
                    document.getElementsByTagName('body')[0].clientWidth,
                session: state.session,
                login: state.login
            };
        case REFRESH_SESSION_REQUEST: // 
            return {...state, loading: true, width: state.width, session: state.session, login: state.login};
        case API_WEB_LOGIN_REQUEST: //  
            return {
                ...state,
                loading: state.loading,
                width: state.width,
                session: state.session,
                login: {
                    loading: true,
                    error: {
                        error: false,
                        type: null
                    },
                    isLogin: false //false      , true
                }
            };
        case API_WEB_LOGIN_SUCCESS: //        
            return {
                ...state,
                loading: false,
                width: state.width,
                session: action.session,
                login: {
                    loading: false,
                    error: {
                        error: false,
                        type: null
                    },
                    isLogin: true
                }
            };
        case API_WEB_LOGIN_FAILURE: //  
            return {
                ...state,
                loading: false,
                width: state.width,
                session: initialState.session,
                login: {
                    loading: false,
                    error: action.error,
                    isLogin: false
                }
            };
        case API_WEB_LOGOUT_REQUEST:// 
            return initialState;
        default:
            return state
    }
}