import React from 'react';
import styles from "./ClientInfo.scss";
import {Link} from "react-router-dom";
import classnames from 'classnames';

const cx = classnames.bind(styles);

class ClientInfo extends React.Component {

    constructor(props) {
        super(props);

        this.LogoutHandler = this.LogoutHandler.bind(this);

    }

    LogoutHandler(e) {
        e.preventDefault();
        this.props.logout();
    };

    render() {

        let items = [
            {
                url: '/mypage/auth',
                boardName: ' '
            },
            {
                url: '/mypage/password',
                boardName: ' '
            },
            {
                url: '/mypage/address',
                boardName: ' '
            },
            {
                url: '/mypage/setting',
                boardName: ' '
            }
        ];


        return (
            <div className={cx(styles['client-info-component'], this.props.active && styles['active'])}>
                <div className={styles['client-info-component--triangle']}></div>
                <div className={styles['client-info-component--head']}>
                    <img src={this.props.thumbnail || require('./icn-no-baby@2x.png')} alt={'  .'}/>
                    <div className={styles['client-info-component--head--desc']}>
                        <p><span></span></p>
                        <div className={styles['client-info-component--head--desc__activity']}>
                            <span> : {this.props.grade === 0 ? '' : '??'}</span>
                            <span> : {this.props.point || 0} P</span>
                        </div>
                    </div>
                    <div className={styles['client-info-component--head--desc__more']}>
                        <a href="javascript:void(0)" role="button" className={styles['__more-info-button']}></a>
                        <div className={styles['client-info-component--head--desc__more--bottom-bar']}></div>
                    </div>
                </div>
                <ul className={styles['client-info-component__list']}>
                    {
                        items ? items.map((item, key) => {
                            return <li key={key}><Link to={item.url}>{item.boardName}</Link></li>
                        }) : <li>   .</li>

                    }
                </ul>
                <div className={styles['client-info-component__logout']}>
                    <Link to="/logout" onClick={this.LogoutHandler}></Link>
                </div>
            </div>
        )
    }
}

export default ClientInfo